import { Plural, Trans } from '@lingui/react'
import React from 'react'
import styled from 'styled-components'

import { PrimaryButton } from '../../common/Buttons'
import { ShopAddress } from '../../common/ShopAddress'
import { IShopStatusRender, ShopStatus } from '../../common/ShopStatus'
import { GPickupBranch } from '../../graph/types'
import { i18n } from '../../i18n'

interface IProps {
  branch: GPickupBranch.Fragment
  onConfirm(): void
}

const PickupMapHeaderStyled = styled.div`
  background-color: ${({ theme }) => theme.primary};
  color: ${({ theme }) => theme.tertiaryText};
  text-align: center;
  padding: 0.4rem 0.2rem 0.2rem 0.2rem;
  height: 2rem;
  vertical-align: middle;
`

export const PickupMapBranch: React.SFC<IProps> = ({ branch, onConfirm }) => (
  <div className="pickup__map">
    <PickupMapHeaderStyled className="pickup__map__header">
      {branch.name}
    </PickupMapHeaderStyled>
    <div className="pickup__map__info">
      <ShopAddress address={branch.address} />
    </div>
    <ShopStatus
      status={branch}
      render={opts => (
        <PrimaryButton
          color="primary"
          data-testid="submitPickupBranch"
          disabled={!branch.pickupEnabled}
          onClick={onConfirm}
        >
          {branch.pickupEnabled ? (
            getButtonText(opts)
          ) : (
            <Trans>Delivery only</Trans>
          )}
        </PrimaryButton>
      )}
    />
  </div>
)

function getButtonText({ decision, openingIn, closingIn }: IShopStatusRender) {
  switch (decision) {
    case 'Closed':
      return <Trans>Closed</Trans>
    case 'ClosingSoon':
      return (
        <Plural
          value={closingIn}
          one="Closing in 1 minute"
          other="Closing in # minutes"
        />
      )
    case 'OpeningSoon':
      return (
        <Plural
          value={openingIn}
          one="Opening in 1 minute"
          other="Opening in # minutes"
        />
      )
    default:
      return i18n.t`Pickup here`.toLocaleUpperCase()
  }
}
