import React from 'react'

import { WithRoot } from '../../common/WithRoot'

import { ShopHeader } from './ShopHeader'
import { QueryShopHeader } from './ShopHeader.graphql'

interface IProps {}

export const ShopHeaderView: React.SFC<IProps> = props => (
  <WithRoot
    observe={true}
    render={root => (
      <QueryShopHeader
        // need to use branchId from order first as it reflects branch for which
        // order is being made and it might have different eg. business hours
        // then the one obtained through business code
        variables={{ branchId: root.order.branchId || root.business.branchId! }}
        skip={!root.business.isValid}
        renderLoading={false}
        render={({ branch }, { loading }) => (
          <ShopHeader branch={branch} isLoading={loading} />
        )}
      />
    )}
  />
)

export default ShopHeaderView
