import React from 'react'

import { ShopAddress } from '../../common/ShopAddress'
import { WithRoot } from '../../common/WithRoot'
import { DeliveryTypeEnum, GShopHeader } from '../../graph/types'
import { i18n } from '../../i18n'

interface IProps {
  address?: GShopHeader.Address
}

export const OrderAddress: React.SFC<IProps> = ({ address }) => (
  <WithRoot
    render={root => {
      switch (root.order.orderMethod) {
        case DeliveryTypeEnum.Messenger:
          return (
            <span>
              {i18n.t`Order to address`}: <b>{root.location.address}</b>
            </span>
          )
        case DeliveryTypeEnum.Pickup:
          return (
            <span>
              {i18n.t`Pickup at address`}:{' '}
              <b>
                <ShopAddress address={address} />
              </b>
            </span>
          )
        default:
          return null
      }
    }}
  />
)
