import './PickupMap.css'

import { useEffectOnce } from '@speedlo/hooks'
import { useObserver } from 'mobx-react-lite'
import React, { useCallback, useEffect, useState } from 'react'
import { InfoWindow, Marker } from 'react-google-maps'

import { useRoot } from '../../common/WithRoot'
import { GPickupBranch } from '../../graph/types'
import { gpsToLatLng } from '../../helpers/gps'
import { RM } from '../../helpers/ramda'
import { MapPanning } from './atoms/MapPanning'
import { QueryPickupMapBranch, QueryPickupMapBrand } from './PickupMap.graphql'
import { PickupMapBranch } from './PickupMapBranch'

interface IProps {
  onConfirm(): void
}

export const PickupMap: React.FC<IProps> = ({ onConfirm }) => {
  const { order, business, map } = useRoot()
  const [activeBranch, setActiveBranch] = useState<MaybeID>(null)

  useEffectOnce(() => {
    if (business.branchId) {
      setActiveBranch(business.branchId)
    }
  })

  useEffect(
    () => {
      if (activeBranch) {
        order.prefetchFoodMenu(activeBranch)
      }
    },
    [activeBranch],
  )

  const onConfirmPickup = useCallback(
    async (branchId: ID) => {
      // it's necessary to set asap so correct data are displayed further
      order.setBranch(branchId)
      await order.createPickupOrder(branchId)
      onConfirm()
    },
    [onConfirm],
  )

  const renderSite = (branch: GPickupBranch.Fragment) => (
    <Marker
      key={branch.id}
      position={gpsToLatLng(branch.gps)}
      onClick={() => setActiveBranch(branch.id)}
    >
      {activeBranch === branch.id ? (
        <InfoWindow
          onCloseClick={() => setActiveBranch(null)}
          defaultOptions={{ maxWidth: 200 }}
        >
          <PickupMapBranch
            branch={branch}
            onConfirm={() => {
              onConfirmPickup(branch.id)
            }}
          />
        </InfoWindow>
      ) : null}
    </Marker>
  )

  return useObserver(() => {
    if (business.isBrand) {
      return (
        <QueryPickupMapBrand
          variables={{ brandId: business.brandId! }}
          renderLoading={false}
          render={({ branches }) => {
            const branch =
              branches && branches.find(RM.propEq('id', business.branchId))
            return (
              <>
                <MapPanning map={map} gps={branch && branch.gps} />
                {branches && branches.map(renderSite)}
              </>
            )
          }}
        />
      )
    }
    return (
      <QueryPickupMapBranch
        variables={{ branchId: business.branchId! }}
        renderLoading={false}
        render={({ branch }) => (
          <>
            <MapPanning map={map} gps={branch && branch.gps} />
            {branch && renderSite(branch)}
          </>
        )}
      />
    )
  })
}
