import React from 'react'

import { TOrderModel } from '../../models/OrderModel'
import { OrderAgreements } from './OrderAgreements'
import { OrderFormInputs } from './OrderFormInputs'
import { OrderFormSetup } from './OrderFormSetup'
import { OrderPaymentMethod } from './OrderPaymentMethod'
import { OrderSubmitLine } from './OrderSubmitLine'

interface IProps {
  order: TOrderModel
}

export const OrderForm: React.SFC<IProps> = ({ order }) => (
  <OrderFormSetup
    order={order}
    render={form => (
      <div className="order__form">
        <div className="order__form__inner">
          <OrderFormInputs />
          <OrderPaymentMethod />
          <OrderAgreements />
        </div>
        <OrderSubmitLine
          onClick={form.submitForm}
          canSubmit={order.isVamInitialized}
        />
      </div>
    )}
  />
)
