import './OrderFormView.css'

import React from 'react'

import { WithOrder } from '../../common/WithOrder'
import { OrderErrorModal } from './OrderErrorModal'
import { OrderForm } from './OrderForm'
import { QueryOrderForm } from './OrderFormView.graphql'
import { OrderGoPayModal } from './OrderGoPayModal'
import { OrderTimePicker } from './OrderTimePicker'

export const OrderFormView: React.SFC = () => (
  <WithOrder
    render={orderModel => (
      <>
        <QueryOrderForm
          variables={{ orderId: orderModel.orderId! }}
          renderLoading={false}
          render={({ order }) => {
            return !order || order.countWares === 0 ? null : (
              <OrderForm order={orderModel} />
            )
          }}
        />
        <OrderGoPayModal order={orderModel} />
        <OrderErrorModal />
        <OrderTimePicker />
      </>
    )}
  />
)
