import './ShopCloseAlert.css'

import { Plural, Trans } from '@lingui/react'
import React from 'react'
import { Alert } from 'reactstrap'

import { ShopStatus } from '../../common/ShopStatus'
import { GCompanyBranchStatus } from '../../graph/types'

interface IProps {
  status?: GCompanyBranchStatus.Fragment
  now?: Date
}

export const ShopCloseAlert: React.SFC<IProps> = ({ status, now }) => {
  return status ? (
    <ShopStatus
      now={now}
      status={status}
      render={({ decision, closingIn }) => {
        if (decision === 'ClosingSoon') {
          return (
            <Alert className="shop__header__alert">
              <Trans>
                Beware! We are
                <Plural
                  value={closingIn}
                  one="closing in 1 minute"
                  other="closing in # minutes"
                />
                . Finish your order quickly.
              </Trans>
            </Alert>
          )
        }
        return null
      }}
    />
  ) : null
}
