import React from 'react'
import { Route } from 'react-router-dom'
import { Container } from 'reactstrap'

import { FinishView } from '../segments/finish/FinishView'
import { MapView } from '../segments/map/views/MapView'
import { ShopHeaderView } from '../segments/shop/ShopHeaderView'
import { ShopOverview } from '../segments/shop/ShopOverview'

const DesktopApp = () => (
  <Container fluid={true}>
    <Route path="/" component={ShopHeaderView} />
    <Route exact={true} path="/" component={MapView} />
    <Route path="/order" component={ShopOverview} />
    <Route path="/finish" component={FinishView} />
  </Container>
)

export default DesktopApp
