import React from 'react'
import styled from 'styled-components'

import { GShopHeader } from '../../graph/types'
import { i18n } from '../../i18n'
import { ShopStatusDisplay } from './ShopStatusDisplay'

interface IProps {
  branch: GShopHeader.Branch
}

const ContainerStyled = styled.div`
  margin: 0.4rem 0 0 0.1rem;
`

const OpeningHoursStyled = styled.div`
  margin: -0.2rem 0 0 1.35rem;
  display: inline-block;
  vertical-align: middle;
`

const StatusDisplayPosition = styled.span`
  padding-left: 2rem;
`

export const ShopOpeningHours: React.SFC<IProps> = ({ branch }) => (
  <ContainerStyled>
    <span className="flaticon-clock" />
    {branch &&
      branch.businessHours.map(businessHour => (
        <OpeningHoursStyled data-testid="openingHours" key={businessHour.id}>
          {businessHour.closed
            ? businessHour.closedNote || i18n.t`Closed`
            : `${businessHour.openAt} - ${businessHour.closeAt}`}
        </OpeningHoursStyled>
      ))}
    <StatusDisplayPosition>
      <ShopStatusDisplay
        status={branch}
        deliveryTime={branch.defaultDeliveryTime}
        pickupTime={branch.defaultPickupTime}
      />
    </StatusDisplayPosition>
  </ContainerStyled>
)
