import gql from 'graphql-tag'

import { buildQuery } from '../../graph'
import {
  AddressFragment,
  CompanyBranchStatusFragment,
} from '../../graph/fragments.graphql'
import { GShopHeader } from '../../graph/types'

export const ShopHeaderQuery = gql`
  query GShopHeader($branchId: ID!) {
    branch: companyBranch(id: $branchId) {
      id
      name
      phone
      homepageUrl
      headerText
      defaultDeliveryTime
      defaultPickupTime
      address {
        ...GAddress
      }
      ...GCompanyBranchStatus
    }
  }
  ${AddressFragment}
  ${CompanyBranchStatusFragment}
`

export const QueryShopHeader = buildQuery<
  GShopHeader.Query,
  GShopHeader.Variables
>(ShopHeaderQuery)
